async function getCode(codeType: number, fromVoiceCodeModal: boolean, retry = 0) {
  if (sendCodeBtnStatus.value === SendCodeBtnStatus.COOLING && !fromVoiceCodeModal) {
    return
  }

  if (!mobile.value) {
    showToast(_T(''))
    reportCodeResult(`${country.value}`, `${mobile.value}`, 'failed', ``, codeType)
    return
  }
  if (!country.value) {
    showToast(_T('/'))
    reportCodeResult(`${country.value}`, `${mobile.value}`, 'failed', `/`, codeType)
    return
  }

  const number = `+${country.value}  ${mobile.value}`
  if (!validateMobile(number)) {
    showToast(_T(''))
    reportCodeResult(
      `${country.value}`,
      `${mobile.value}`,
      'failed',
      ``,
      codeType
    )
    return
  }

  if (loading.value) {
    return
  }
  loading.value = true
  try {
    reportClick('send_code', `${mobile.value}`, `${country.value}`)
  } catch (error) {
    reportError(error)
  }

  // 
