    mobile.value,
    country.value,
    codeType,
    showPageTime,
    /* eslint-disable no-unneeded-ternary */
    fromVoiceCodeModal && codeType === 0 ? true : false,
    'login'
  )
  loading.value = false

  console.log('getMobileCode:', success, result, data, message)

  // {
  //   // device token
  //   showModal(TokenModal, {
  //     cb: () => {
  //       refreshDeviceToken(mobile.value, country.value, countryFlag.value)
  //     },
  //   })
  //   return
  // }
  if (success && result === 0) {
    //
  } else {
    reportEvent(Events.user_login_report_failed, {
      source: 'sawa',
      status_string: isDiwania.value ? 'diwaniaH5_mobile' : 'sawaksaH5_mobile',
      error_code: `${result}`,
      error_content: message || '',
      is_sawaapp_authorize: false,
      h5_type: 'phone',
      type: '',
      h5_scene: 'get_code',
    })
  }
  reportCodeResult(
    `${country.value}`,
    `${mobile.value}`,
    result === 0 ? 'succeed' : 'failed',
    `${message || ''}`,
    codeType
  )

  if (success && result === 0) {
    if (data?.more_code_type?.length > 0) {
      showVoiceCodeModal('')
    } else {
      cooldown.start(getSysTs())
