  }
}

async function login() {
  // 
  const v2 = getAppClientVersion()
  const oldVersion = getPlatform().isIPhone && (!v2 || Number(v2) < 10258)
  if (oldVersion) {
    oldLogin()
    return
  }

  //   
  if (!mobile.value) {
