    reportClick('submit', `${mobile.value}`, `${country.value}`)
  } catch (error) {
    reportError(error)
  }
  if (loading.value) return
  loading.value = true
  const [success, resp] = await queryBindedAccounts({
    country_code: country.value,
    mobile: String(mobile.value),
    code: String(code.value),
    not_auto_create: true,
  })
  if (!success) {
    showToast(_T(''))
    return
  }
  loading.value = false
  const { result, data, message, toast } = resp
  const { identities, login_key, can_create } = data || {}
  console.log('checkBindedLogin', result, data, message)
  // if (mobile.value === '99992025') {
  //   return
  // }
  console.warn('[mobile]', mobile.value)

  // 1) 
  if (result !== 0) {
    reportEvent(Events.user_login_report_failed, {
      source: 'sawa',
      status_string: isDiwania.value ? 'diwaniaH5_mobile' : 'sawaksaH5_mobile',
      error_code: `${result}`,
      error_content: message || '',
      is_sawaapp_authorize: false,
      h5_type: 'phone',
      type: '',
      h5_scene: 'login',
    })
    if (result === 20001) {
      showBanToast(message || '')
      return
    }
    if (result === 20193) {
      unregisterRemind(resp)
      return
    }
    if (result === 20312) {
      showCreateModal(() => createAccount(login_key), 'phone')
      return
    }
    if (result === 20063) {
      showToast(_T('ip'))
      return
    }
    if (result === 20064) {
      showToast(_T(''))
      return
    }
    if (toast) {
      // 
      showToast(toast)
    }
    return
  }

  // 2)  user_info 
  if (data && data.user_info && data.user_info.session_key) {
    session_key.value = data.user_info.session_key
    refreshAccount({ data }, 'sawaksa_mobile', isDiwania.value ? 'diwaniaH5_mobile' : 'sawaksaH5_mobile')
    return
  }

  // 3) user_info 
  const accounts = identities.filter((t: any) =>
    [UserSource.KSA, UserSource.DIWANIA, UserSource.LUDO].includes(t.user_source)
  )
  if (accounts.length === 0) {
    showCreateModal(() => createAccount(login_key), 'phone')
    return
  }

  // 4) 1  ksa
  if (accounts.length === 1 && !can_create) {
    const p1 = accounts[0]
    const [success, resp] = await selectBindedAccount({
      login_key: p1.login_key,
      uid: p1.uid,
    })
    const { result, data, message } = resp || {}
    if (success && data && data.user_info && data.user_info.session_key) {
      refreshAccount({ data }, 'sawaksa_mobile', isDiwania.value ? 'diwaniaH5_mobile' : 'sawaksaH5_mobile')
      return
    }
    if (result === 20001) {
      showBanToast(message || '')
      return
    }
    if (result === 20193) {
      unregisterRemind(resp)
      return
    }
    if (result === 20312) {
      showCreateModal(() => createAccount(login_key), 'phone')
      return
    }
    if (result === 20063) {
      showToast(_T('ip'))
      return
    }
    if (result === 20064) {
      showToast(_T(''))
      return
    }
    showToast(_T(''))
    return
  }

  // 5) 
  goAccountsSelectingPage(resp, String(mobile.value), String(code.value), 'sawaksa_mobile')
}

async function oldLogin(auto_create = false) {
  //   
  if (!mobile.value) {
