    return
  }
  loading.value = true
  //  session_key
  const [success, resp] = await loginWithMobile(
    mobile.value,
    code.value,
    country.value,
    auto_create
  )
  const { result, data, message, toast } = resp
  console.log('loginWithMobile:', success, result, data, message)
  if (!success) {
    loading.value = false
    showToast(_T(''))
    return
  }
  // 
  if (result !== 0) {
    loading.value = false
    if (result === 20193) {
      unregisterRemind(resp)
    }
    if (result === 20312) {
      showCreateModal(() => oldLogin(true), 'phone')
    }
    if (result === 20063) {
      showToast(_T('ip'))
    }
    if (result === 20064) {
      showToast(_T(''))
    }
    if (result === 32200) {
      // 
      showToast(toast)
    }
    if (toast) {
      showToast(toast)
    }
    return
  }

  if (data && data.user_info && data.user_info.session_key) {
    session_key.value = data.user_info.session_key
    refreshAccount({ data }, 'sawaksa_mobile', isDiwania.value ? 'diwaniaH5_mobile' : 'sawaksaH5_mobile')
  }
  loading.value = false
}

